import React from "react";
import { useAframeState } from "./StateContext";
import { useWalletContext } from "./WalletContext";
import { claimLoot, mintEmpty } from "../actions";
import { randomBetween } from "../helpers";
export default function () {
    var state = useAframeState();
    var wallet = useWalletContext().wallet;
    var handleKill = function () {
        console.log("Kill!");
        if (randomBetween(0, 10) < 5) {
            claimLoot(wallet.provider);
        }
        else {
            mintEmpty(wallet.provider, state.enemy.species);
        }
    };
    return (React.createElement("ul", null,
        React.createElement("li", { className: "menu-item" },
            React.createElement("button", { onClick: handleKill }, "Fight (Kill instantly)")),
        React.createElement("li", { className: "menu-item" },
            React.createElement("button", { onClick: function () { return console.log("Tame!"); } },
                "Tame ",
                state.enemy.species))));
}
