import AFRAME from "aframe";
/**
 *
 */
AFRAME.registerComponent("combat-trigger", {
    schema: {},
    init: function () {
        var _this = this;
        this.el.addEventListener("click", function (event) {
            _this.el.sceneEl.emit("enterCombat", { id: event.target.id });
            console.log(_this.el.attributes.animation);
        });
    },
});
