import randomBetween from "./randomBetween";
export default function randomStep(args) {
    var current = args.current, min = args.min, max = args.max;
    var dx = randomBetween(min.x, max.x);
    var dy = randomBetween(min.y, max.y);
    var dz = randomBetween(min.z, max.z);
    return {
        x: current.x + dx,
        y: current.y + dy,
        z: current.z + dz,
    };
}
